    color: #102654;
    text-decoration: none;
    cursor: pointer; }
    .contact__item a:hover, .contact__item a:focus {
      color: rgba(16, 38, 84, 0.7); }

.contact__first-column {
  position: relative;
  box-sizing: border-box;
  width: 51%;
  border-right: 1px solid #74819c; }
  .contact__first-column::before {
    content: "";
    position: absolute;
    top: 0;
    right: -2px;
    width: 5px;
    height: 6px;
    background: white; }
  .contact__first-column::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: -2px;
    width: 5px;
    height: 22px;
    background: white; }

.contact__second-column {
  box-sizing: border-box;
  width: 49%;
  padding-left: 55px; }

@media (max-width: 1199px) {
