.social__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.social__item {
  position: relative;
  display: inline-flex;
  height: 24px;
  vertical-align: middle;
  margin-right: 20px; }
  .social__item--vk {
    width: 24px;
    height: 13px;
    margin-bottom: 0;
    margin-top: 8px; }
  .social__item--ig {
    width: 24px;
    height: 24px;
    margin-top: 4px; }
  .social__item--fb {
    width: 12px;
    height: 24px;
    margin-top: 4px; }
  .social__item:last-child {
    margin-right: 0; }

.social__link {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  vertical-align: middle; }
  .social__link:hover, .social__link:focus {
    opacity: 0.5; }
  .social__link:active {
