.social__link--ig {
  width: 24px;
  height: 24px;
  background: url("../img/insta.svg") no-repeat center center; }

.social__link--facebook {
  width: 12px;
  height: 24px;
  background: url("../img/fb.svg") no-repeat center center; }

.social__link--vk {
  width: 24px;
  height: 13px;
  background: url("../img/vk.svg") no-repeat center center; }

@media (max-width: 1199px) {
  .social__list {
    width: 100px; } }

@media (max-width: 767px) {
  .social__list {
    width: 143px; }
  .social__item {
    height: 27px; }
    .social__item--vk {
      width: 32px;
      height: 17px;
      margin: auto 0; }
    .social__item--ig {
      width: 27px;
      height: 27px;
      margin-top: 4px; }
    .social__item--fb {
